.main-nav {
  width: 100%;
  margin: 0 0 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include vp-1279 {
    margin: 0 0 0 20px;
  }

  @include vp-1023 {
    width: auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0;
  }

  &__toggle {
    position: relative;
    display: none;
    width: 35px;
    height: 20px;
    padding: 25px 0;
    cursor: pointer;
    user-select: none;

    @include vp-1023 {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    span {
      display: block;
      position: absolute;
      left: 0px;
      width: 35px;
      //width: calc(100% - 10px * 2);
      height: 4px;
      background-color: rgb(0, 174, 0);
      transition: transform $trans-default, opacity $trans-default, visibility $trans-default;

      &:nth-child(1) {
        top: 14px;
      }

      &:nth-child(2) {
        top: 50%;

        transform: translateY(-50%);
      }

      &:nth-child(3) {
        bottom: 14px;
      }
    }

    @include hover-focus {
      opacity: 0.8;
    }
  }

  &__menu {
    display: flex;
    align-items: center;
    gap: 0 10px;
    width: 100%;

    @include vp-1279 {
      justify-content: flex-end;
    }

    @include vp-1023 {
      z-index: 10;
      position: fixed;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      overflow: auto;

      top: 80px;
      right: 0;
      bottom: 0;
      left: 0;

      width: 100%;
      padding: 30px 44px 60px;

      background-color: $color-default-white;

      transition: transform $trans-default, visibility $trans-default, opacity $trans-default;
      transform: translateX(100%);
    }

    @include vp-767 {
      padding: 20px 45px 40px;
    }

    @include vp-389 {
      padding: 20px 35px 40px;
    }
  }

  &__list {
    display: flex;
    padding: 0;
    flex-wrap: wrap;
    list-style: none;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin: 0;

    @media screen and (max-width: 1380px) {
      gap: 0px;
    }

    // @include vp-1023 {
    //   flex-direction: column;
    // }
  }

  &__item {
    position: relative;
    margin: 0;

    @include vp-1439 {
      margin: 0;
    }

    @include vp-1279 {
      margin: 0;
    }

    @include vp-1023 {
      margin: 0;
    }

    &.menu-item-has-children {
      // .main-nav__link {
      //   &::after {
      //     content: "";
      //     width: 16px;
      //     height: 16px;
      //     margin-left: 4px;

      //     background-image: url("../img/sprite/icon-chevron-down.svg");
      //     background-position: 0 0;
      //     background-repeat: no-repeat;

      //     transition: transform $trans-default;
      //   }
      // }

      @media (min-width: 1023px) {
        @include focus-within {
          .main-nav__link {
            color: rgb(47, 156, 180) !important;

            &::after {
              content: "";
            }

            svg {
              color: rgb(47, 156, 180);

              // transform: rotate(180deg);
            }
          }

          .main-nav__sublist {
            opacity: 1;
            visibility: visible;
          }
        }

        @include hover-focus {
          .main-nav__link {
            color: rgb(47, 156, 180);

            &::after {
              content: "";
            }

            svg {
              color: rgb(47, 156, 180);

              // transform: rotate(180deg);
            }
          }

          .main-nav__sublist {
            opacity: 1;
            visibility: visible;
          }
        }
      }
    }

    &.menu-item-has-children.is-active {
      .main-nav__link {
        @media screen and (max-width: 1280px) {
          color: rgb(47, 156, 180);

          svg {
            rect {
              fill: rgb(47, 156, 180);
            }
          }
        }

        /*svg {
          @include vp-1023 {
            color: rgb(24, 44, 143);

            transform: rotate(180deg);
          }
        }*/
      }

      .main-nav__sublist {
        @include vp-1023 {
          display: block;
          opacity: 1;
          visibility: visible;
        }
      }
    }

    &:last-child {
      margin: 0;
    }
  }

  &__link {
    color: rgb(255, 255, 255);
    font-family: Onest;
    font-size: 18px;
    font-weight: 400;
    line-height: 100%;
    margin: 0;
    transition: .3s;
    display: flex;
    flex-direction: row-reverse;
    gap: 5px;
    align-items: center;
    transition: .2s;
    // transform: translate3d(0, 0, 0);
    text-transform: none;
    // padding: 30px 12px;

    @include vp-1023 {
      font-size: 21px;
      line-height: 20px;
      flex-direction: row;
    }

    &::after {
      content: none;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 10px;
    }

    svg {
      flex-shrink: 0;

      width: 32px;
      height: 32px;
      margin: 0;

      color: $color-default-black;

      transition: color $trans-default, transform $trans-default;
    }

    @include hover-focus {
      color: rgb(47, 156, 180);

      svg {
        path {
          fill: rgb(47, 156, 180);
        }
      }
    }

    @include active {
      color: rgb(47, 156, 180);

      svg {
        color: rgb(47, 156, 180);
      }
    }

    @media screen and (max-width: 540px) {
      font-size: 18px;
    }

    @media screen and (max-width: 430px) {
      font-size: 16px;
    }

    @media screen and (max-width: 390px) {
      font-size: 14px;
    }
  }

