  .main-nav__list li:hover>.main-nav__sublist {
    display: block;
  }

  // .main-nav__sublist li:hover>.main-nav__sublist1 {
  //   display: block;
  // }

  .main-nav__sublist li {
    position: relative;
  }

  &__sublist {
    z-index: 10;
    position: absolute;
    top: calc(100% + 0px);
    margin: 0;
    list-style: none;
    border-radius: 8px;
    transition: transform $trans-default, opacity $trans-default, visibility $trans-default;
    display: none;

    /*@include vp-1023 {
      position: relative;
      display: none;
      top: 0;
      left: 0;

      width: auto;
      min-width: unset;
      padding: 0;
      margin: 30px 0;
      border-radius: 0;
      background-color: $color-transparent;
      box-shadow: none;

      opacity: 1;
      visibility: visible;
    }*/
  }

  &__sublist::after {
    content: '';
    background-image: url(../img/sprite/bg-sublist.svg);
    width: 270px;
    height: 300px;
    display: block;
    position: absolute;
    filter: blur(40px);
    top: 5%;
    left: 6%;
    z-index: -1;
  }

  // .main-nav__sublist {
