  &__subitem {
    margin: 0 0 30px;

    @media screen and (max-width: 390px) {
      margin: 0 0 20px;
    }

    &:last-child {
      margin: 0;
    }
  }

  &__sublink {
    display: inline-block;
    max-width: 230px;
    transition: .2s;

    // @media screen and (max-width: 1380px) {
    //   display: inline;
    // }

    // @include vp-767 {
    //   font-size: 18px;
    //   line-height: 16px;
    // }

    @media screen and (max-width: 540px) {
      font-size: 18px;
    }

    @media screen and (max-width: 430px) {
      font-size: 16px;
    }

    @media screen and (max-width: 390px) {
      max-width: 200px;
      font-size: 14px;
    }

    @include hover-focus {
      color: rgb(47, 156, 180);
      //border-bottom: 2px solid rgb(0, 174, 0);

      .active {
        color: rgb(47, 156, 180);
      }

      svg {
        path {
          fill: rgb(0, 174, 0);
        }
      }
    }

    @include active {
      color: rgb(47, 156, 180);
    }

    &__active {
      background-color: rgb(47, 156, 180) !important;
    }
  }

  &__contacts {
    text-align: right;

    @media screen and (max-width: 1023px) {
      gap: 35px;
    }

    // @media screen and (max-width: 391px) {
