    color: rgb(255, 255, 255);
    font-family: Onest;
    font-size: 16px;
    font-weight: 500;
    line-height: 100%;
    transition: .3s;

    @include vp-1023 {

      margin-top: -60px;
      font-size: 20px;
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 20px;
      line-height: 16px;
    }

    @media screen and (max-width: 540px) {
