    }

    @include active {
      color: rgb(47, 156, 180);
    }
  }

  &__email {
    color: rgb(255, 255, 255);
    font-family: Onest;
    font-size: 18px;
    font-weight: 700;
    line-height: 100%;

    transition: .3s;

    @include vp-1023 {
      font-size: 20px;
      line-height: 20px;
      margin-bottom: 10px;
    }

    @include vp-767 {
