.container {
  width: 100%;
  max-width: 1420px;
  margin: 0 auto;
  padding: 23px 40px;
}

.header {
  position: relative;
}

.header {
  width: 100%;
  background: rgb(3, 26, 36);
}



.header__menu-link {
  display: flex;
  gap: 5px;
  align-items: center;
  transition: .2s;
}

.header__menu-link:hover>.header__menu-link-text {
  transition: .2s;
  color: rgb(47, 156, 180);
}

.header__menu-link:hover>svg {
  transition: .2s;

  rect {
    fill: rgb(47, 156, 180);
  }
}

.header__menu-link-text {
  color: rgb(255, 255, 255);
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 100%;
  margin: 0;
}

.header__right {
  display: flex;
  align-items: center;
  gap: 35px
}

.header__btn {
  position: relative;
  z-index: 2;
  max-width: 240px;
  width: 100%;
  border-radius: 4px;
  background: linear-gradient(90.48deg, rgb(5, 70, 93) -12.82%, rgb(43, 145, 167) 47.227%, rgb(34, 114, 132) 104.3%);
  color: rgb(255, 255, 255);
  font-family: Onest;
  font-size: 18px;
  font-weight: 500;
  line-height: 140%;
  padding: 15px 40px;

  @media screen and (max-width: 1023px) {
    display: none;
  }
}

.header__btn::before {
  position: absolute;
  content: "";
  inset: 0;
  color: rgb(255, 255, 255);
  font-family: Onest;
  font-size: 18px;
  font-weight: 500;
  line-height: 140%;
  padding: 15px 40px;
  border-radius: 4px;
  background: linear-gradient(90.96deg, rgb(34, 114, 132) -14.603%, rgb(43, 145, 167) 42.471%, rgb(5, 70, 93) 102.517%);
  z-index: -1;
  opacity: 0;
  transition: opacity .2s linear;
}

.header__btn:hover::before {
  opacity: 1;
}

.header.is-open .main-nav__toggle {
  background: transparent;
}

.header.is-open .main-nav {
  position: relative;
}



.header.is-open .main-nav__toggle span:nth-child(1) {
  top: 20px;
  -webkit-transform: rotate(-47deg) translate(-8px, 4px) !important;
  -ms-transform: rotate(-45deg) translate(0, -50%);
  transform: rotate(-45deg) translate(0, -50%);
  background: rgb(0, 174, 0);
  height: 4px;
}

.header.is-open .main-nav__toggle span:nth-child(2) {
  opacity: 0;
  visibility: hidden;
}

.header.is-open .main-nav__toggle span:nth-child(3) {
  top: 20px !important;
  -webkit-transform: rotate(45deg) translate(0, 50%);
  -ms-transform: rotate(45deg) translate(0, 50%);
  transform: rotate(45deg) translate(0, 50%);
  background: rgb(0, 174, 0);
}

.header.is-open .main-nav__menu {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}

.header__logo,
.header__wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 40px;
}

.header__wrapper {
  @media screen and (max-width: 1023px) {
    display: block;
  }
}

.header__logo {
  position: relative;
  width: 200px;
  height: 50px;
  justify-content: start;
  flex-shrink: 0;

  @media screen and (max-width: 1023px) {
    margin-bottom: 20px;
  }

  @media screen and (max-width: 540px) {
    width: 130px;
    height: 33px;
  }
}

.main-nav__toggle {
  position: relative;
  display: none;
  width: 33px;
  height: 22px;
  padding: 20px 0;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: opacity rgb(255, 255, 255);
  -o-transition: opacity rgb(255, 255, 255);
  transition: opacity rgb(255, 255, 255);
}

.main-nav__toggle span {
  display: block;
  position: absolute;
  left: 10px;
  width: calc(100% - 20px);
  height: 4px;
  border-radius: 0 !important;
  background-color: rgb(255, 255, 255);
  -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
}

.main-nav__toggle span:nth-child(1) {
  top: 12px !important;
}

.main-nav__toggle span:nth-child(2) {
  top: 50%;

  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.main-nav__toggle span:nth-child(3) {
  bottom: 12px !important;
}

.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}

.main-nav__list,
.main-nav__menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

