.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  position: relative;
  margin: 0 70px 0 0;
}

.main-nav__item:last-child,
.main-nav__subitem:last-child {
  margin: 0;
}

.main-nav__link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 100%;
  color: #1a1a1a;
  text-transform: uppercase;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);

}

.main-nav__link::after {
  content: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 10px;
}

.main-nav__link svg {
  -ms-flex-negative: 0;
  flex-shrink: 0;
  color: #000;
  -webkit-transition: color 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: color 0.3s ease, transform 0.3s ease;
  transition: color 0.3s ease, transform 0.3s ease;
  transition: color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
}



.main-nav__link.focus-visible:focus,
.main-nav__link.focus-visible:focus svg,
.main-nav__sublink.focus-visible:focus {
  color: #182c8f;
}

.main-nav__sublist {
  display: none;
  width: 315px;
  gap: 0 50px;
  z-index: 10;
  position: absolute;
  top: calc(100% + 0px);
  left: 0px;
  margin: 0;
  padding: 30px 30px 40px;
  list-style: none;
  border-radius: 8px;
  background: rgb(3, 26, 36);
  border: 1px solid rgb(34, 114, 132);
  //border-image: linear-gradient(45deg, #05465D, #2B91A7, #227284) 1;
  -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  opacity: 0;
  visibility: hidden;

  @media screen and (max-width: 1380px) {
    max-width: 600px;
    // left: -200px;
  }

  @media screen and (max-width: 390px) {
    width: 280px;
    padding: 20px;
  }
}

