.main-nav__subitem {
  margin: 10px 0px 30px;
}

.main-nav__sublink {
  display: -webkit-box;
  display: -ms-flexbox;
  //display: block;
  color: rgb(255, 255, 255);
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 100%;
  width: 100%;
  transition: .2s;

  @media screen and (max-width: 1380px) {
    width: 100%;
  }
}

.main-nav__subitem:last-child>.main-nav__sublink {
  color: rgb(47, 156, 180);
  font-family: Onest;
  font-size: 16px;
  font-weight: 500;
  line-height: 140%;
  background: rgba(36, 35, 35, 0.5);
  padding: 7px;
  text-align: center;
  position: relative;
  transition: .2s;

  @media screen and (max-width: 540px) {
    font-size: 18px;
  }

  @media screen and (max-width: 430px) {
    font-size: 16px;
  }

  @media screen and (max-width: 390px) {
    font-size: 14px;
  }
}

.main-nav__subitem:last-child>.main-nav__sublink:hover {
  color: rgb(34, 114, 132);
}

.main-nav__subitem:last-child>.main-nav__sublink::before {
  content: '';
  position: absolute;
  inset: 0;
  border: 1px solid;
  border-radius: 8px;
  background-image: linear-gradient(to right, rgb(34, 114, 132), rgb(43, 145, 167), rgb(5, 70, 93)) 1;
  transition: .2s;
}

.main-nav__subitem:last-child>.main-nav__sublink:hover::before {
  background-image: linear-gradient(to left, rgb(34, 114, 132), rgb(43, 145, 167), rgb(5, 70, 93)) 1;
  opacity: 0.8;
}

.main-nav__sublist1 {
  display: none;
}

.main-nav__subitem1 {
  margin-top: 20px;
}

.main-nav__sublist li:hover>.main-nav__sublist1 {
  display: block;
}

.main-nav__sublist li {
  position: relative;
}




.main-nav__sublink_active {
  background-color: rgb !important;
}

.main-nav__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  gap: 15px 0;

  @media screen and (max-width: 1023px) {
    margin: 0;
    flex-direction: column-reverse;
  }
}

.main-nav__phone {

  font-weight: 600;
  line-height: 100%;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

.btn.focus-visible:focus,
.main-nav__email.focus-visible:focus,
.main-nav__phone.focus-visible:focus {
  color: rgb(224, 0, 0);
}

.main-nav__email {
  color: #c20000;
  font-family: Inter;
  font-weight: 600;
  line-height: 100%;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

@media (min-width: 1023px) {
  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__link {
    color: rgb(47, 156, 180);
  }

  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__link::after {
    content: "";
  }

  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__link svg {
    color: rgb(47, 156, 180);
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
  }

  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__sublist {
    opacity: 1;
    visibility: visible;
  }

  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__link {
    color: rgb(47, 156, 180);
  }

  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__link::after {
    content: "";
  }

  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__link svg {
    color: rgb(47, 156, 180);
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
  }

  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__sublist {
    opacity: 1;
    visibility: visible;
  }
}


@media (min-width: 1023px) and (hover: hover) {
  .main-nav__item.menu-item-has-children:hover .main-nav__link {
    color: rgb(0, 174, 0);
  }

  .main-nav__item.menu-item-has-children:hover:not(.focus-visible) .main-nav__link::after {
    content: "";
  }

  .main-nav__item.menu-item-has-children:hover:not(.focus-visible) .main-nav__link svg {
    color: rgb(0, 174, 0);

    // -webkit-transform: rotate(180deg);
    // -ms-transform: rotate(180deg);
    // transform: rotate(180deg);
    rect {
      fill: rgb(47, 156, 180);
    }
  }

  .main-nav__item.menu-item-has-children .main-nav__sublist {
    opacity: 1;
    visibility: visible;
  }

  .main-nav__item.menu-item-has-children:hover:not(.focus-visible) .main-nav__sublist {
    opacity: 1;
    visibility: visible;
  }
}



@media (max-width: 1439px) {
  // .container {
  //   padding: 0 40px;
  // }

  .main-nav__item {
    margin: 0 30px 0 0;
  }
}



