@media (max-width: 768px) {
  .container {
    padding: 20px 45px;
  }

  .main-nav__menu {
    padding: 20px 45px 40px;
  }

  .main-nav__sublink {
    font-size: 18px;
    line-height: 16px;
  }

  // .main-nav__contacts {
  //   margin: 60px 0 0;
  // }

  .main-nav__sublink1 {
    font-size: 16px;
  }

}

@media screen and (max-width: 700px) {
  .header__search-block--mobile {
    display: none;
  }

  .header__search-icon-mobile {
    display: flex;
    align-items: center;
    margin: 0 0 0 auto;
    cursor: pointer;
    transition: .2s;

    svg {
      width: 35px;
      height: 35px;
    }
  }

  .header__search-icon-mobile:hover {
    opacity: 0.7;
  }
}

@media (max-width: 390px) {
  .container {
    padding: 20px;
  }

  .header__position--mobile {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 20px;
    margin-bottom: 70px;
  }

  .header__position--mobile::after {
    padding-top: 60px;
  }

  .main-nav__menu {
    padding: 20px 35px 40px;
  }

}